.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.todo-container{
  width:1200px;
  margin: auto;
}
.todo{
  margin:auto;
  min-height: 70px;
  height:auto;
  border: 5px solid #ffffff;
  background-color:#ffffff;
  
}
.todo-id{
  min-width: 70px;
  min-height: 70px;
  text-align: center;
  border: 5px solid #19b113;
  border-radius: 20px;
  background-color:#19b113;
}
.todo-task{
  min-width: 300px;
  min-height: 70px;
  border: 5px solid #19b113;
  border-radius: 20px;
  text-align: center;
  background-color: #19b113;
}
.todo-time{
  min-width: 170px;
  min-height: 70px;
  border: 5px solid #19b113;
  border-radius: 20px;
  text-align: center;
  background-color: #19b113;
}
.todo-button{
  min-width: 150px;
  min-height: 70px;
  border: 5px solid #19b113;
  border-radius: 20px;
  text-align: center;
  background-color: #b83228;
}
.text{
  text-align: center;
  font: 20px Arial, sans-serif;
  font-weight: bold;
  color: #ffffff;
}
.text-time{
  text-align: center;
  font: 15px Arial, sans-serif;
  color: #ffffff;
}
.button{
  margin:10px;
  width: auto;
  height: auto;
  border: 0px  #b83228;
  text-align: center;
  background-color: #b83228;
}
.form {
  display: flex;
  flex-wrap: wrap;
  
}

.input {
  background: #000000;
  border: none;
  background: hsl(0 0% 93%);
  border-radius: .25rem;
  padding: .75rem 1rem;
}
.submit{
    background:  #b83228;
    color: white;
    box-shadow: 0 .75rem .5rem -.5rem hsl(0 50% 80%);
  }
